.almefy-auth-v1 {
  font-size: .875em;
  font-family: sans-serif;
  font-weight: normal;
  line-height: 120%;

  .almefy-auth-container {
    position: relative;

    .almefy-placeholder {
      position: relative;
      width: 480px;
      max-width: 100%;
      vertical-align: middle;
      border-style: none;
    }

    .almefy-deep-link {
      display: inline-block;
      cursor: initial;
      background-size: contain;

      &.active {
        cursor: pointer;
      }
    }

    .almefy-loader {
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      margin-left: -1.5rem;
      margin-top: -1.5rem;
      width: 3rem;
      height: 3rem;
      vertical-align: text-bottom;
      border: 0.25em solid #aaaaaa;
      border-right-color: transparent;
      border-radius: 50%;
      animation: almefy-auth-v1 .75s linear infinite;
      box-sizing: border-box;
    }

    .almefy-dialog {
      position: absolute;
      border-radius: 5px;
      padding: 7px 8px 8px;
      background-color: #9a9a9a;
      color:  #fff;
      width: 100%;
      margin-left: auto;
      margin-right: auto;
      top: 50%;
      transform: translateY(-50%);
      display: block;
      box-sizing: border-box;

      p.almefy-message {
        text-align: center;
        margin-bottom: 5%;
        padding-bottom: 0;
      }

      a.almefy-button {
        display: block;
        text-decoration: none;
        text-align: center;
        padding: 2% 0;
        background-color: #777;
        border-radius: .3em;
        color: #fff;

        &:hover {
          background-color: #666;
          color: #fff;
        }

        svg {
          vertical-align: -1px;
        }
      }

      &.type-log {
        background-color: lighten(#2b5d8c, 7%);
        a.almefy-button {
          background-color: #224e77;
          color: #fff;
          &:hover {
            background-color: darken(#224e77, 7%);
            color: #fff;
          }
        }
      }

      &.type-error {
        background-color: lighten(#9a0000, 7%);
        a.almefy-button {
          background-color: #9a0000;
          color: #fff;
          &:hover {
            background-color: darken(#9a0000, 7%);
            color: #fff;
          }
        }
      }

    }

  }

  .almefy-link {
    margin-top: 7.5%;
    display: block;
    text-decoration: none;
    text-align: center;
    padding: 2% 0;
    background-color: #888;
    border-radius: .3em;
    color: #fff;

    &:hover {
      background-color: #666;
      color: #fff;
    }

    svg {
      vertical-align: -2px;
    }
  }

  .almefy-claim {
    padding: 3% 0 5%;
    text-align: center;

    img {
      display: inline-block;
      height: 17px;
    }
  }

  &.boxed-style {
    padding: 7% 7% 0;
    background-color: #dbdbdb;
    border-radius: .5em;
  }

  .almefy-instance-id {
    font-family: monospace;
    font-size: 10px;
    color: #666;
    padding: 2% 0;
    text-align: center;
  }

  @keyframes almefy-auth-v1 {
    to { transform: rotate(360deg); }
  }
}
